<template>
  <div class="clearfix">
    
     <el-select v-model="type" placeholder=""  @change="handleChange">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
         >
        </el-option>
     </el-select>
    <el-button type="deflaut" class="pull-right" @click="addWork"></el-button>
     <el-table
      :data="tableData"
      style="width: 100%;margin-top:30px;border:2px solid #ccc;">
      <el-table-column
        prop="work_title"
        label=""
       >
      </el-table-column>
      <el-table-column
         prop="pic_url"
         label=""
      >
       <template slot-scope="scope">
          <img :src="scope.row.pic_url" alt="" height="40px" >
       </template>
      </el-table-column>
      <el-table-column
        prop="view_count"
        label=""
        >
      </el-table-column>
      <el-table-column
        prop="work_type"
        label="">
      </el-table-column>
      <el-table-column
        prop="home_show"
        label="">
        <template slot-scope="scope">
          <span>{{(scope.row.home_show=='1')?'':''}}</span>
       </template>
      </el-table-column>
      <el-table-column
        width="250"
        prop="handle"
        label=""
        type="index"
        :index="index"
      >
        <template slot-scope="scope">
            <el-button type="danger" size="mini" @click="handleDelete(scope.row.work_id,scope.row.pic_name,scope.$index)"></el-button>
            <el-button type="info" size="mini" @click="handleEdit(scope.row.work_id)"></el-button>
            <el-button type="success" size="mini" @click="handleDetail(scope.row.work_id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
    style="margin-top:20px;"
    class="pull-right"
    layout="prev, pager, next"
    :page-size="limit"
    @current-change = 'pageChange'
    :total="total">
  </el-pagination>
  </div>
   
  </template>

  <script>
import { work_select,delete_work } from 'api/works';
import { comment_delete_all } from 'api/comment';

export default {
  async mounted(){
    // 
    let { count,list } = await work_select({limit:this.limit,currentPage:this.currentPage});
    this.total = count;
    this.tableData = list;
  },
  methods:{
    addWork(){
       this.$router.push('/admin/worksAdd');
    },
    // 
    async pageChange(size){
      this.currentPage = size;
      let { list } = await work_select({limit:this.limit,currentPage:this.currentPage});
      this.tableData = list;
    },
    // 
    handleEdit(id){
      this.$router.push("/admin/worksEdit?id="+id);
    },
    // 
    async handleDelete(id,pic_name,index){
      // 
      let res = await delete_work({id,pic_name});
      if(res == 1){
         this.total--;
         this.tableData.splice(index,1);
         //
         comment_delete_all({work_id:id});
      }
    },
    //
    handleDetail(id){
       this.$router.push("/admin/worksDetail?id="+id);
    },
    //
    async handleChange(){
      let { count,list } = await work_select({limit:this.limit,currentPage:this.currentPage,type:this.type});
      this.total = count;
      this.tableData = list;
    }
  },
  data() {
    return {
      index:0,//
      tableData: [], //
      limit:5,  //
      total:0,  //
      currentPage:1, //,
      options: [{     //
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }],
        type: ''
    };
  }
};
</script>


